export default {
	data() {
		return {
			// 金额
			total: 0,
			// 数量
			amount: 0,
			// 点击了某个类目，把这个类目的所有菜品信息存储到food数组当中，然后显示出来
			food: [],
			// 这个用来存储从服务器返回的所有菜品信息
			allFood: [],
			// 记录点击类目的id,默认第一个激活
			activeIndex: 0,
			// 这个用来存储从服务器返回的第一个类目
			firstLeimu: '',
			// 这个用来存储从服务器返回的类目数据
			leimu: []
		}
	},
	created() {
		this.InitLeimu()
		this.InitFood()
		this.Init()
	},
	methods: {
		// 初始化金额和数量
		Init() {
			this.total=0
			this.amount=0
			var arr = uni.getStorageSync('recommend')
			if (arr.length > 0) {
				for (var i in arr) {
					if (arr[i].quality > 0) {
						this.total += arr[i].quality * arr[i].price
						this.amount += 1
					}
				}
			}
			// console.log(this.amount)
		},
		toShoppingBag() {
			if(this.total===0){
				uni.showToast({
				    title: '请选择菜品',
				    duration: 2000,
					icon:'none'
				});
				return;
			}
			uni.navigateTo({
				url: '../shopBag/shopBag'
			})
		},
		// 减
		sub(item,index){
			var arr = uni.getStorageSync('recommend')
			if (arr.length > 0) {
				for (var i in arr) {
					if (arr[i].id === item.id) {
						arr[i].quality -= 1
						uni.setStorageSync('recommend', arr)
					}
				}
			}
			this.getActive(index,item.type)
			this.Init()
		},
		// 加
		add(item,index) {
			var tableNum=uni.getStorageSync('tableNum')
			//点加号之前必须先扫码桌号
			if (!tableNum) {
				wx.showModal({
					title: '提示',
					content: '请在首页扫码识别桌号后再来点餐',
					showCancel: false, //去掉取消按钮
					success: function(res) {
						if (res.confirm) {
							wx.switchTab({
								url: '../../index',
							})
						}
					}
				})
				return;
			}
			// console.log(item)
			var arr = uni.getStorageSync('recommend')||[]
			if (arr.length > 0) {
				for (var i in arr) {
					if (arr[i].id === item.id) {
						arr[i].quality += 1
						uni.setStorageSync('recommend', arr)
						this.getActive(index,item.type)
						this.Init()
						return false
					}
				}
				
			}
			// console.log(this.food)
			item.quality=0
			arr.push(item)
			uni.setStorageSync('recommend', arr)
			for (var i in arr) {
				if (arr[i].id === item.id) {
					arr[i].quality += 1
					uni.setStorageSync('recommend', arr)
					this.getActive(index,item.type)
				}
			}
			this.Init()
			
		},
		// 激活的类目
		getActive(index, leimu) {
			this.activeIndex = index

			this.food = []
			this.allFood.forEach((item, index) => {

				if (item.type === leimu) {
					this.food.push(item)
				}
			})
			var arr = uni.getStorageSync('recommend')
			for (var i in this.food) {
				for (var j in arr) {
					if (this.food[i].id === arr[j].id) {
						this.food[i].quality = arr[j].quality
					}
				}
			}
			// console.log(this.food)
		},
		// 初始化类目
		InitLeimu() {
			uni.request({
				url: 'https://www.linhua.club/ordering/wx/getAllType',
				method: "GET",
				success: (res) => {
					// console.log(res.data)
					this.leimu = res.data.leimu
					this.leimu.forEach((item, index) => {

						if (index === 0) {
							this.firstLeimu = item.leimu
						}
					})
				}
			})
		},
		// 初始化菜品
		InitFood() {
			uni.request({
				url: 'https://www.linhua.club/ordering/wx/getAllFood',
				method: "GET",
				success: (res) => {
					this.allFood = res.data.allFood
					
					
					this.allFood.forEach((item, index) => {
						item.quality=0
						if (item.type === this.firstLeimu) {
							this.food.push(item)
						}
					})
					console.log(this.food)
					// 将显示的菜品数量和缓存的菜品数量一一对应
					var arr = uni.getStorageSync('recommend')
					for (var i in this.food) {
						for (var j in arr) {
							if (this.food[i].id === arr[j].id) {
								this.food[i].quality = arr[j].quality
							}
						}
					}
				}
			})
		}
	}
}
